.write__post {
	textarea {
		width: 100%;
		font-size: 24px;
		height: 100px;
		resize: none;
		border-radius: 5px;
		border: none;
		outline: none;
	}
}
.write__btn {
	margin: 5px 0px 0px 2px;
	padding: 5px 2px;
	background-color: var(--color-text);
	outline: none;
	border: none;
	width: 100%;
	border-radius: 5px;
	text-decoration: none;
	cursor: pointer;
	&:hover {
		-moz-box-shadow: 0 0 3px #000;
		-webkit-box-shadow: 0 0 3px #000;
		box-shadow: 0 0 3px #000;
	}
}
